import React from 'react';
import { styled, useTheme } from '@mui/material/styles';

import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';



//icons
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';
import InfoRoundedIcon from '@mui/icons-material/InfoRounded';
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

import PersonalCard from '../../Cards/Card';

export default function PublicidadTab() {

    const myStyle = {
        display:"flex",
        height: "100%",
        minWidth: "20%",
        flexDirection:'column',
        padding:'1rem',

    }
    
    const myStyle2 ={
        display:"flex",
        height: "100%",
        minWidth: "55%",
        flexDirection:'column',
        padding:'1rem'
    }

    return(
        <Box sx={{width:"100%", height:"100%", display:"flex"}}>
                            <Box sx={myStyle}>
                                <Box sx={{display:"flex", justifyContent:"space-between"}}>
                                    <Typography sx={{textAlign:'center',color:'white'}} variant='h6'>Presentación</Typography>
                                    <InfoRoundedIcon sx={{ mr: 1 , color:"white"}} fontSize="small"/>
                                </Box>

                                <Box sx={{display:"flex", alignItems:"center", width:"100%", height:"70%"}}>
                                    <Box sx={{
                                        height:"3rem",
                                        width:"45%",
                                        }}>
                                             <img src={require("../../../../assets/images/Locales/Vista1_1_1.jpg")} style={{ width: "100%", height: "100%", objectFit: "scale-down" }}></img>
                                    </Box>
                                    <Box>
                                        <Box sx={{display:"flex", marginTop:"0.5rem",marginLeft:"0.2rem"}}>
                                            <FolderRoundedIcon sx={{ mr: 1 , color:"white", fontSize:"0.8rem"}} />
                                            <Typography sx={{color:'white', fontSize:"0.8rem"}}>Elegir archivo</Typography>
                                        </Box>

                                        <Box sx={{display:"flex", marginTop:"0.5rem",marginLeft:"0.2rem"}}>
                                            <PhotoCameraIcon sx={{ mr: 1 , color:"white", fontSize:"0.8rem"}} />
                                            <Typography sx={{color:'white', fontSize:"0.8rem"}}>Tomar foto</Typography>
                                        </Box>
                                    </Box>
                                </Box>
                                   
                            </Box>
                            <Divider orientation="vertical" variant="middle" flexItem />
                            <Box sx={myStyle2}>
                                <Typography sx={{color:'white'}} variant='h6'>Vallas publicitarias</Typography>
                                <Box sx={{display:"flex", marginTop:"0.5rem", justifyContent:'space-between' ,width:"100%"}}>
                                    
                                    <Box sx={{display:"flex"}}>
                                        <PersonalCard number="A"></PersonalCard>
                                        <PersonalCard number="B"></PersonalCard>
                                        <PersonalCard number="C"></PersonalCard>
                                        <PersonalCard number="D"></PersonalCard>
                                    </Box>

                                    <Box sx={{display:'flex', alignContent:'flex-end'}}>
                                        <Typography sx={{color:'white'}}>Agregar colaborador</Typography>
                                        <AddCircleOutlineRoundedIcon sx={{ mr: 1 , color:"white"}} />
                                    </Box>
                                </Box>
                                
                            </Box>
                        </Box>
    );
}